/**
 * Created by THINK on 2017/5/28.
 * 自定义表单验证规则
 */
import { Validator } from 'vee-validate';

/**
 * 手机号验证规则
 */
Validator.extend('phone', {
    messages: {
        zh_CN:field => '手机号码输入不正确',
    },
    validate: value => {
        return value.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value);
    }
});
Validator.extend('myPhoneNumeric', {
    messages: {
        zh_CN:field => '手机号码只能是数字',
    },
    validate: value => {
        return  /^([0-9]+)$/.test(value);
    }
});
Validator.extend('myNumeric', {
    messages: {
        zh_CN:field => '验证码只能是数字',
    },
    validate: value => {
        return  /^([0-9]+)$/.test(value);
    }
});

Validator.extend('code', {
        messages: {
            zh_CN:field=>'验证码必须4位或6位',

        },
        validate: value => {
            return  /^(\d{4}){1}(\d{2})?$/.test(value);
        }
    });
Validator.extend('pwd', {
    messages: {
        zh_CN:field=>'密码必须是6到13位数字或字母组成',
    },
    validate: value => {
        return  /^[a-zA-Z0-9]{6,13}$/.test(value);
    }
});
export default Validator;



