 <template>
    <div class="forget-layer">
        <header class="forget-title-box">
            <a onclick="window.history.go(-1);" class="forget-back"></a>
            <h2 class="forget-title">重新设置密码</h2>
        </header>
        <main class="forget-info-box">
            <div class="forget-phone">
                <input  class="forget-phone-number forget-common" placeholder="请输入手机号" v-validate="'required|myPhoneNumeric|phone'" :class="{'input': true, 'danger': errors.has('phone') }" name="phone" type="tel" v-model="phone"/>
                <span v-show="errors.has('phone')" class="text-danger">{{ errors.first('phone') }}</span>
            </div>
            <div class="forget-code">
                <input type="text" class="forget-code-number forget-common" placeholder="请输入验证码" v-validate="'required|myNumeric|code'" :class="{'input': true, 'danger': errors.has('code') }" name="code" v-model="code"/>
                <timer-btn ref="timerbtn" class="forgetg-code" id="get-code" v-on:run="sendCode" ></timer-btn>
                <span v-show="errors.has('code')" class="text-danger">{{ errors.first('code') }}</span>
            </div>
            <div class="forget-password">
                <input type="password" class="forget-password-number forget-common" placeholder="请输入密码"autocomplete="off" v-validate="'required|pwd'" :class="{'input': true, 'danger': errors.has('pwd') }" name="pwd" v-model="pwd"/>
                <span v-show="errors.has('pwd')" class="text-danger">{{ errors.first('pwd') }}</span>
            </div>
            <div class="forget-password">
                <input type="password" class="forget-password-number-again forget-common" placeholder="请再次输入密码"autocomplete="off" v-validate="'required|pwd|confirmed:pwd'" :class="{'input': true, 'danger': errors.has('pwd') }" name="pwd" v-model="repwd"/>
                <span v-show="errors.has('pwd')" class="text-danger">{{ errors.first('pwd') }}</span>
            </div>
            <div class="forget-btn">
                <a @click="okClick($event)" class="forget-btn-info">确定</a>
            </div>
        </main>
    </div>
</template>
<script>
    import BtnMsg from '../TimeBtn.vue';
    import Api from '../../../api/api';
    import {getVerCode} from '../../../api/register';
    import {dealRegister} from '../../../api/register';
    import customValidate from '../../../assets/js/customValidate';
    export default{
        data(){
            return{
                phone:'', //手机号
                code:'',  //验证码
                pwd:'',   //密码
                repwd:'', //第二次密码
            }
        },
        mounted(){
            $(".forget-phone-number")[0].focus();
            this.changeInStyle();
        },
        methods:{
            /**
             * 改变样式
             **/
            changeInStyle(){
                const _that = this;
                let inputs = $(".forget-common");
                $(inputs).each(function (index,item) {
                    $(item).focus(function () {
                        $(this).css({
                            'border':'2px solid #20a0ff',
                        });
                    });
                    $(item).blur(function () {
                        $(this).css({
                            'border': '1px solid #d6d6d6',
                        });
                    });
                });
            },
            /**
             *
             * 重置密码
             * */
            okClick($event){
                const that = this;
              this.$validator.validateAll().then((success)=>{
                  if(!success){
                      return;
                  }
                dealRegister(Api.RERESTPASSWORD,that.phone,that.code,that.pwd,that.repwd).then((res)=>{
                  let resData = res.data;
                  if(resData.code === '100'){
                    that.showMsg("重置密码成功!");
                  }
                  that.$router.push({path: '/login', replace: true});
                },(err)=>{
                  that.showMsg("重置密码失败，请重试!");
                  that.$store.dispatch('hideLoading');
                });
              },(err)=>{
                that.showMsg("请填写正确的信息!");
              });
                $event.preventDefault();
            },
            showMsg(str){
                this.$toast({
                    message: str,
                    duration: 2000,
                });
            },
            /**
             * 获取重置密码功能的验证码
             */
            sendCode:function(){
                    const _that =this;
                    this.$validator.validate('phone',_that.phone).then(function (success) {
                        if(!success){
                            _that.showMsg("手机号输入不正确!");
                        }else{
                            _that.$refs.timerbtn.setDisabled(true); //设置按钮不可用
                            const codeEle = $("#get-code");
                            codeEle.css({
                                'background-color':'#20a0ff',
                                'color':'#ffffff'
                            });
                            _that.$refs.timerbtn.start(); //启动倒计时
                            getVerCode(Api.RESETCODE,_that.phone).then((res)=>{
                                if(res.status === 201 && res.data!==null){
                                    _that.showMsg("获取验证码成功!");
                                }
                                _that.$refs.timerbtn.stop(); //停止倒计时
                            },(err)=>{
                                _that.$store.dispatch('hideLoading');
                                _that.showMsg("获取验证码失败，稍后请重试!");
                            });
                        }

                    });
                },

            /**
             *
             * 输入框在获取到焦点的时候的样式
             **/
            getContent(){
                const codeContentEle =  $("#reg-code-number");
                codeContentEle.css({
                    'border':'2px solid #20a0ff',
                })
            },
            /**
             * 改变输入框失去焦点时候的样式
             */
            blurContent(){
                const codeContentEle =  $("#reg-code-number");
                codeContentEle.css({
                    'border': '1px solid #d6d6d6',
                })
            }
        },
        components:{
            'timer-btn':BtnMsg,
        }
    }
</script>
<style scoped>
    @import "../../../assets/css/forgetpwd.css";
    .text-danger{
        color: #a94442;
        width: 100%;
        display: block;
        text-align: left;
    }
    .danger{
        border: 2px solid #a94442;
    }
</style>



